import React, { useRef, useState, useEffect, useLayoutEffect } from 'react'
import { Link, useParams } from 'react-router-dom'
import styles from './PostDetailModal.module.scss'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faVolumeMute, faVolumeUp } from '@fortawesome/free-solid-svg-icons'
import { AiFillCloseCircle, AiOutlineHeart } from 'react-icons/ai'
import { FaAngleDown, FaAngleUp, FaCommentDots, FaPlay } from 'react-icons/fa'
import logo from '../../assets/img/logo.png'
import Button from '../../components/common/Button'
import Modal from 'react-modal'
import Login from '../Login'
import Register from '../../components/Register'
import { useDispatch, useSelector } from 'react-redux'
import { hideModal, loginModal, registerModal } from '../../redux/actions'
import userImage from '../../assets/img/user.jpg'
import axiosInstance from '../../axiosInstance'

import {
  FacebookShareButton,
  WhatsappShareButton,
  TwitterShareButton,
  FacebookIcon,
  WhatsappIcon,
  TwitterIcon
} from 'react-share'

const defaultFn = () => {}
const PostDetailModal = ({
  isMuted = false,
  onCloseBtn = defaultFn,
  onToggleMute = defaultFn,
  onNextVideo = defaultFn,
  onPrevVideo = defaultFn,
  isPrevVideo = false,
  isNextVideo = false,
  data,
  videoId
}) => {
  const customStyles = {
    content: {
      position: 'absolute',
      top: '50%',
      left: '50%',
      right: 'auto',
      bottom: 'auto',
      marginRight: '-50%',
      transform: 'translate(-50%, -50%)',
      border: '1px solid #ccc',
      background: '#fff',
      overflow: 'auto',
      WebkitOverflowScrolling: 'touch',
      borderRadius: '4px',
      outline: 'none',
      padding: '20px',
      zIndex: 1000
    },
    overlay: {
      position: 'fixed',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'rgba(255, 255, 255, 0.75)'
    }
  }

  const token = window.localStorage.getItem('token')
  const [comments, setComments] = useState([])
  const [isValueInput, setIsValueInput] = useState(false)
  const [valueInput, setValueInput] = useState('')
  const LOGIN_MODAL = 'LOGIN_MODAL'
  const REGISTER_MODAL = 'REGISTER_MODAL'
  const videoRef = useRef(null)
  const [isPlaying, setIsPlaying] = useState(false)
  const [currentTime, setCurrentTime] = useState(0)
  const imageRef = useRef(null)
  const image = useRef(null)
  const modalLogin = useSelector(state => state.modal.modalLogin)
  const modalRegister = useSelector(state => state.modal.modalRegister)
  const dispatch = useDispatch()
  const getVideoRef = ref => {
    if (ref) {
      videoRef.current = ref
    }
  }

  useEffect(() => {
    let isMounted = true
    videoRef.current.ontimeupdate = function () {
      if (isMounted) {
        const getTime = videoRef.current.currentTime
        setCurrentTime(getTime)
      }
    }
    return () => {
      isMounted = false
    }
  }, [currentTime])

  useLayoutEffect(() => {
    if (videoId === null) return
    axiosInstance
      .get(`/api/posts/${videoId}/comments`)
      .then(res => {
        setComments(res.data)
      })
      .catch(err => {
        console.log(err)
      })
  }, [])

  const handlePausePlayVideo = () => {
    if (videoRef.current.paused) {
      videoRef.current.currentTime = currentTime
      videoRef.current.play()
      setIsPlaying(!isPlaying)
    } else {
      videoRef.current.pause()
      setIsPlaying(true)
    }
  }

  const handlePlayVideo = () => {
    videoRef.current.play()
    setIsPlaying(!isPlaying)
  }

  const handleErrorImage = () => {
    imageRef.current.src = userImage
  }

  const handleError = () => {
    if (!image.current) return null
    image.current.src = userImage
  }

  const openModal = () => {
    const action = loginModal()
    dispatch(action)
  }

  const closeModal = () => {
    const action = hideModal()
    dispatch(action)
  }

  const handleShowModalRegister = () => {
    const action = registerModal()
    dispatch(action)
  }

  const handleShowModalLogin = () => {
    const action = loginModal()
    dispatch(action)
  }

  const handleSubmit = e => {
    e.preventDefault()
    if (valueInput === '') return
    axiosInstance
      .post(`/api/posts/${videoId}/comments`, {
        comment: valueInput
      })
      .then(res => {
        const newComment = res.data
        setComments([newComment, ...comments])
        setValueInput('')
      })
      .catch(err => {
        console.log(err)
      })
  }

  const handleDeleteComment = post => {
    axiosInstance.delete(`/api/comments/${post.id}`).catch(err => {
      console.log(err)
    })
    const commentIndex = comments.findIndex(comment => comment.id === post.id)
    const newComment = comments.splice(commentIndex, 1)
    setComments(newComment)
  }

  const handleChangeValue = e => {
    setValueInput(e.target.value)
  }

  useEffect(() => {
    if (valueInput !== '') {
      setIsValueInput(true)
    } else {
      setIsValueInput(false)
    }
  }, [valueInput])
  return (
    <>
      <div className={styles.videoFixed}>
        <div className={styles.videoContainer}>
          <div className={styles.thumbnail}>
            <img src={data.thumb_url} alt="anh video" className={styles.image} />
          </div>
          <video
            ref={getVideoRef}
            className={styles.video}
            src={data.file_url}
            muted={isMuted}
            loop
            poster={data.thumb_url}
            autoPlay
            onClick={handlePausePlayVideo}
          />
          {isPlaying ? (
            <button className={styles.btnPlay} onClick={handlePlayVideo}>
              <FaPlay className={styles.playIcon} />
            </button>
          ) : (
            ''
          )}
          <img src={logo} alt="logo" className={styles.logo} />

          <button className={styles.btnIcon} onClick={onCloseBtn}>
            <AiFillCloseCircle className={styles.icon} />
          </button>
          <button className={[styles.btn, styles.vlBtn].join(' ')} onClick={() => onToggleMute(data)}>
            <FontAwesomeIcon className={styles.vlIcon} icon={isMuted ? faVolumeMute : faVolumeUp} />
          </button>
          {!isNextVideo ? (
            <div className={`${styles.downBtn} ${styles.btnChervon}`} onClick={onNextVideo}>
              <FaAngleDown className={`${styles.icon} ${styles.iconDown}`} />
            </div>
          ) : (
            ''
          )}
          {!isPrevVideo ? (
            <div className={`${styles.upBtn} ${styles.btnChervon}`} onClick={onPrevVideo}>
              <FaAngleUp className={`${styles.icon} ${styles.iconUp}`} />
            </div>
          ) : (
            ''
          )}
        </div>
        <div className={styles.contentContainer}>
          <div className={styles.userInfoContainer}>
            <div className={styles.userInfo}>
              <div to={`/@${data.user.nickname}`}>
                <img
                  className={styles.avatar}
                  src={data.user.avatar}
                  alt="avatar"
                  onError={handleErrorImage}
                  ref={imageRef}
                />
              </div>
              <div className={styles.userInfoLink}>
                <Link className={styles.nickname} to={`/@${data.user.nickname}`}>
                  {data.user.nickname}
                </Link>
                <Link className={styles.firstName} to={`/@${data.user.nickname}`}>
                  {data.user.first_name}
                </Link>
              </div>
              <Button
                children="Follow"
                size="xl"
                color="colorPrimary"
                border
                type="default"
                hoverPrimaryColor
                onClick={openModal}
              />
              <Modal isOpen={modalLogin === LOGIN_MODAL} style={customStyles}>
                <Login
                  title="Logout to Tiktok"
                  accountTitle="Ban a co tai khoan"
                  loginText="ng nhp"
                  onShowModalLogin={handleShowModalRegister}
                  onCloseModal={closeModal}
                />
              </Modal>
              <Modal isOpen={modalRegister === REGISTER_MODAL} style={customStyles}>
                <Register
                  title="Login to Tiktok"
                  accountTitle="Ban khng co tai khoan"
                  loginText="ng ky"
                  onShowModalRegister={handleShowModalLogin}
                  onCloseModal={closeModal}
                />
              </Modal>
            </div>
          </div>
          <div className={styles.videoInfoContainer}>
            <div className={styles.actionContainer}>
              <div className={styles.action} style={{ display: 'flex' }}>
                <div className={styles.actionItem}>
                  <div className={styles.iconWrap} onClick={handleShowModalLogin}>
                    <AiOutlineHeart className={styles.icon} />
                  </div>
                  <span className={styles.votes}>{data.likes_count}</span>
                </div>
                <div className={styles.actionItem}>
                  <div className={styles.iconWrap}>
                    <FaCommentDots className={styles.icon} />
                  </div>
                  <span className={styles.votes}>{data.comments_count}</span>
                </div>
              </div>
              <div className={styles.actionRight}>
                <div className={styles.shareContainer}>
                  <span>Chia se vi</span>
                  <div className={styles.shareGroup}>
                    <WhatsappShareButton
                      className={styles.shareBtn}
                      url={`http://www.facebook.com/sharer.php?u=${window.location.href}`}
                    >
                      <WhatsappIcon className={styles.iconFa} size={32} round />
                    </WhatsappShareButton>
                    <FacebookShareButton
                      className={styles.shareBtn}
                      url={`http://www.facebook.com/sharer.php?u=${window.location.href}`}
                    >
                      <FacebookIcon className={styles.iconFa} size={32} round />
                    </FacebookShareButton>
                    <TwitterShareButton url={`http://www.facebook.com/sharer.php?u=${window.location.href}`}>
                      <TwitterIcon className={styles.iconFa} size={32} round />
                    </TwitterShareButton>
                  </div>
                </div>
              </div>
            </div>
            <div className={styles.copyLinkContainer}>
              <div className={styles.linkContainer}>{window.location.href}</div>
              <div className={styles.copyContainer}>Sao chep lin kt</div>
            </div>
          </div>
          {!token ? (
            <div className={styles.commentContainer}>
              <div className={styles.logoutContainer}>
                <h3>ng nhp  xem binh lun</h3>
                <p className={styles.desc}>ng nhp  xem binh lun va thich video</p>
                <Button children="ng nhp" color="white" size="xxl" type="primary" hover onClick={openModal} />
                <p className={styles.signUp}>
                  Bn khng c ti khon?
                  <span onClick={handleShowModalRegister} className={styles.loginBtn}>
                    ng ky
                  </span>
                </p>
              </div>
            </div>
          ) : (
            <>
              <div className={styles.commentContainer}>
                <div className={styles.comments}>
                  {comments.map(comment => (
                    <div className={styles.commentItem}>
                      <div className={styles.commentContent}>
                        <Link to={`/@${comment.user.nickname}`}>
                          <img
                            onError={handleError}
                            ref={image}
                            src={comment.user.avatar}
                            alt="avatar"
                            className={styles.avatar}
                          />
                        </Link>
                        <div className={styles.commentWrap}>
                          <Link className={styles.nicknameCmt} to={`/@${comment.user.nickname}`}>
                            {comment.user.nickname}
                          </Link>
                          <div className={styles.commentText}>
                            <span className={styles.cmt}>{comment.comment}</span>
                            <div className={styles.bottomContainer}>
                              <span className={styles.commentTime}>1 day ago</span>
                              <span className={styles.reply}>Tra li</span>
                              <span onClick={() => handleDeleteComment(comment)} className={styles.deleteComment}>
                                Xoa
                              </span>
                            </div>
                          </div>
                        </div>
                        <div className={styles.likeContainer}>
                          <AiOutlineHeart className={styles.icon} />
                          <span className={styles.likeCounts}>{comment.likes_count}</span>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>

              <form onSubmit={handleSubmit}>
                <input type="text" placeholder="Thm binh lun" value={valueInput} onChange={handleChangeValue} />
                <button className={isValueInput ? styles.active : ''} type="submit">
                  ng
                </button>
              </form>
            </>
          )}
        </div>
      </div>
    </>
  )
}

export default PostDetailModal
